@import "./variable.scss";
.about {
	grid-area: 2/2/6/6;
	background-color: $secondary-color;
	border: 10px solid $light-color;
	border-radius: 20px;
	padding: 15px 20px 10px;
	& p {
		text-align: justify;
		margin-bottom: 0.5rem;
		font-size: 1.1rem;
		color: $dark-color;
	}
}
@include devices(mobile-2) {
	.about--ts {
		grid-area: 1/1/4/1;
		border-radius: 0;
		border: unset;
		margin-top: 2vh;
		margin-bottom: 2vh;
		background-color: $fourth-color;
		& p {
			font-size: 1.1rem;
			margin-bottom: 0.7rem;
			color: $light-color;
			& > h3 {
				color: $light-color;
			}
		}
	}
}
@include devices(mobile-1) {
	.about--ts {
		grid-area: 1/1/3/1;
	}
}
@include devices(desktop-1) {
	.about {
		grid-area: 1/2/3/11;
	}
}
@include devices(desktop-2) {
	.about {
		grid-area: 2/2/6/7;
	}
}
